@import url('https://fonts.googleapis.com/css2?family=Unbounded:wght@300;500;700;900&display=swap');

*{
  font-family: 'Unbounded', cursive;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #818cf8;
}
.App{
  display: flex;
  max-width: 450px;
  min-width: 400px;
  gap: 1rem;
  width: 45%;
  flex-direction: column;
  margin: 5rem auto;
  box-shadow: 0.1rem 0.2rem 0.1rem 0.2rem black;
  color: white;
  padding: 1rem;
  background-image: linear-gradient(to top, #5f72bd 0%, #9b23ea 100%);
  text-align: center;
  border-radius: 10px;
}
.filter{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3rem;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
}
table{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
table,tr,td,th{
  border:2px solid black;
  padding: 0.3rem;
  border-collapse: collapse;
  margin: auto;
}
.error{
  display: flex;
  background-color: bisque;
  border: 2px solid black;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 1rem;
  color: red;
  transition-duration: 0.5s;
}
.green{
  display: flex;
  background-color: bisque;
  border: 2px solid black;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 1rem;
  color: green;
  transition-duration: 0.5s;
}
.goaway{
  display: none;
}
input{
  width: 145px;
  padding: 0.5rem;
}

#psubmit{
  padding: 0.3rem 0.3rem;
  border: 0;
  width: 200px;
  margin: auto;
  grid-column: 1/3;
  font-weight: bold;
  font-size: 1.3rem;
  transition: transform 0.2s;
  border-radius: 10px;
}
#psubmit:hover{
  transform: translateY(5px) scale(0.9);
  background-color: #454545;
  color: white;

}
.pform{
  display: grid;
  border-radius: 10px;
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: repeat(3,1fr);
  place-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
  animation: slidein 0.2s ease-in;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
}
.goaway{
  display: none;
  animation: slideout;
}

@keyframes slidein {
  from{
    transform: scale(0.2);
  }
}
.info{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  padding: 1rem 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #1c1917;
  color: #a8a29e;
  font-weight: bold;

}

.persons{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(200, 200, 220, 1) 0px 8px 16px -8px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
